import "./footerlist.scss";

export const FooterList = ({ children, title, image }) => {
  return (
    <div className="container">
      <div className="footerlist__top">
        <img src={image} alt="image" />
        <p>{title}</p>
      </div>
      <ul className="footerlist__links">{children}</ul>
    </div>
  );
};
