import "./heroinfo.scss";
import { Select } from "../selects/Select";
import { SelectProps } from "../selects/SelectProps";
import Plus from "../../assets/icons/plus.svg";

export const HeroInfo = () => {
  return (
    <div className="container">
      <div className="hero">
        <div className="hero__inner">
          <Select>
            <SelectProps
              title="Любая категория"
              text="Автомобили"
              text1=" Недвижимость"
              text2=" Электроника"
            />
          </Select>
          <span className="hero__middle-border"></span>
          <button className="hero__middle-btn">Что будем искать ?</button>
          <span className="hero__middle-border"></span>
          <Select>
            <SelectProps
              title="По всей Узбекистану"
              text="  Ташкент Недвижимость  "
              text1=" Ташкент Квартиры"
              text2="  Ташкент Продажа"
            />
          </Select>
          <button className="hero__middle-btn2">Найти</button>
        </div>
        <button className="hero__right-btn">
          <img className="hero__right-btn-img" src={Plus} alt="plus" /> Добавить
          обьявления
        </button>
      </div>
    </div>
  );
};
