import "./profilecomponent.scss";
import ProfileJpeg from "../../assets/icons/profile-comp-img.svg";
import ProfileGreen from "../../assets/icons/profile-green-up.svg";
import ProfileBlue from "../../assets/icons/profile-blue-up.svg";
import ProfileBurger from "../../assets/icons/burger-icon.svg";

export const ProfileComponent = () => {
  return (
    <div className="profilecomp">
      <div className="profilecomp__top">
        <img src={ProfileJpeg} alt="image" />
        <p className="profilecomp__text">Срочно нужен программист yii2</p>
        <div className="profilecomp__cost">
          <p>2 000 000</p>
          <p>6 000 000 сум</p>
        </div>
        <div className="profilecomp__date">
          <p># 345 002</p>
          <p>С: 28 июнь</p>
          <p>По: 28 июль</p>
        </div>
        <button className="profilecomp__btn1">
          <img src={ProfileGreen} alt="up" />
          Поднять
        </button>
        <button className="profilecomp__btn2">
          <img src={ProfileBlue} alt="up" />
          Рекламировать
        </button>
        <button className="profilecomp__burgerbtn">
          <img src={ProfileBurger} alt="menu" />
        </button>
      </div>
      <hr />
      <div className="profilecomp__bottom">
        <p className="profilecomp__bottom-text">Статистика </p>
        <p> Просмотры: 195 </p>
        <p>Тел.: 200</p>
        <p> В Избранном: 0</p>
      </div>
    </div>
  );
};
