import "./profile.scss";
import PlusBlack from "../../assets/icons/plus-black.svg";
import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { useState } from "react";

import { ProfileComponent } from "../../components/ProfileComponents";
import { HeroInfo } from "../../components/HeroInfo";
import { Header } from "../../components/Header/Header";
import { FooterTop } from "../../components/Footer/FooterTop";

export const Profile = () => {
  const [value, setValue] = useState("one");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div>
      <Header />
      <div className="container">
        <HeroInfo />
      </div>
      <div className="container">
        <div className="profile__top">
          <p className="profile__name">Здравствуйте Зухриддин Темиров</p>
          <div className="profile__top-right">
            <p className="profile__money">Ваш счет: 2 3000 сум</p>
            <button className="profile__plusbtn">
              <img className="profile__plus-img" src={PlusBlack} alt="plus" />{" "}
              Пополнить
            </button>
          </div>
        </div>
      </div>
      <hr />
      <div className="container">
        <div className="profile__navs">
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="wrapped label tabs example"
          >
            <Tab
              value="one"
              label="Объявления"
              wrapped
              sx={{ marginRight: "50px" }}
            />
            <Tab value="two" label="Сообщения" sx={{ marginRight: "50px" }} />
            <Tab
              value="three"
              label="Платежи и счёт"
              sx={{ marginRight: "50px" }}
            />
            <Tab value="four" label="Настройки" sx={{ marginRight: "50px" }} />
            <Tab value="five" label=" Мой бизнес" />
          </Tabs>
        </div>
      </div>

      <div className="pageProfile">
        <div className="container">
          <div className="profile__money-info">
            <div className="profile__money-infoleft">
              <p>Фото </p>
              <p> Название </p>
            </div>
            <div className="profile__money-inforight">
              <p>Цена </p>
              <p> Дата </p>
              <p> Действие </p>
            </div>
          </div>
          <div className="profile__infos">
            <ProfileComponent />
            <ProfileComponent />
            <ProfileComponent />
            <ProfileComponent />
            <ProfileComponent />
          </div>
        </div>
      </div>
      <FooterTop bgColor="black" />
    </div>
  );
};
