import "./productpage.scss";

import { HeroInfo } from "../../components/HeroInfo";
import {
  Box,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
  Item,
  Tab,
  Tabs,
  Typography,
} from "@mui/material";
import { useEffect, useState } from "react";
import { BaseUrlProducts } from "../../constants/ConstantsUrl";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { Header } from "../../components/Header/Header";
import { FooterTop } from "../../components/Footer/FooterTop";
import { Footer } from "../../components/Footer/Footer";

export function ProductPage() {
  const [product, setProduct] = useState({});
  const { id } = useParams();

  const [value, setValue] = useState("one");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [products, setProducts] = useState([]);

  useEffect(() => {
    axios
      .get(`${BaseUrlProducts}/products`)
      .then((res) => setProducts(res.data))
      .catch((err) => {
        console.log(err);
      });
  }, []);

  useEffect(() => {
    axios(`${BaseUrlProducts}/products/${id}`)
      .then((res) => setProduct(res.data))
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <div>
      <Header></Header>
      <HeroInfo />

      <div className="container">
        <p className="productpage__text">
          Объявления / Ташкент Недвижимость / Ташкент Квартиры / Ташкент Продажа
          / Новостройки Ташкент
        </p>
        <p className="productpage__title">
          Срочно Голден Хаус Етти Чинор Новостройка 4х ком на 1 этаже <br />{" "}
          юнусабад 5
        </p>
        <div className="productpage__infos">
          <div className="products">
            <img width="500" height="500" src={product.image}></img>
          </div>
          <div className="productpage__right">
            <p className="productpage__right-title">{product.title}</p>
            <p className="productpage__right-price">{product.price} y.e</p>
            <p className="productpage__right-type">{product.category}</p>

            <div className="productpage__btns">
              <button className="productpage__btn">
                99891 166 Покозать польностю
              </button>
              <button className="productpage__btn">
                99891 166 Покозать польностю
              </button>
            </div>

            <p className="productpage__right-text">{product.description}</p>
          </div>
        </div>
      </div>

      <div className="container">
        <Box sx={{ width: "100%", paddingBottom: 2 }}>
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="wrapped label tabs example"
          >
            <Tab
              sx={{ fontWeight: 600 }}
              value="one"
              label="Новые объявления"
              wrapped
            />
            <Tab
              sx={{ fontWeight: 600 }}
              value="two"
              label="Лучщие предложение"
            />
          </Tabs>
        </Box>
      </div>

      <div className="container">
        <Grid
          rowSpacing={1}
          columnSpacing={{ xs: 1, sm: 2, md: 3 }}
          className="products"
        >
          {products.slice(0, 4).map((item) => (
            <Link to={` `}>
              <Card sx={{ width: 265, height: 282, boxShadow: "none" }}>
                <CardActionArea>
                  <CardMedia
                    component="img"
                    height="190"
                    width="262"
                    image={item.image}
                    alt={item.category}
                  />

                  <CardContent>
                    <Typography
                      variant="h5"
                      component="div"
                      className="home__card-title"
                    >
                      {item.category}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="text.secondary"
                      fontWeight="700"
                    >
                      {item.rating.count}
                    </Typography>
                  </CardContent>
                </CardActionArea>
                <CardActions>
                  <Typography variant="body2" color="text.secondary">
                    {item.price} sum
                  </Typography>
                </CardActions>
              </Card>
            </Link>
          ))}
        </Grid>
      </div>
      <FooterTop bgColor="black" />
      <Footer />
    </div>
  );
}
