import "./homehero.scss";
import LeftIcon from "../../assets/icons/left-icon.svg";
import RightIcon from "../../assets/icons/right-icon.svg";
import HeroImg1 from "../../assets/image/Автомобили.png";
import HeroImg2 from "../../assets/image/Недвижимость.png";
import HeroImg3 from "../../assets/image/Электроника.png";
import HeroImg4 from "../../assets/image/Работа.png";
import HeroImg5 from "../../assets/image/Личные вещи.png";
import HeroImg6 from "../../assets/image/Дом и Сад.png";
import HeroImg7 from "../../assets/image/Стройка и ремонт.png";
import HeroImg8 from "../../assets/image/Хобби и Спорт.png";
import HeroImg9 from "../../assets/image/Бизнес и услуги.png";

export function HomeHero() {
  return (
    <div>
      <div className="hero">
        <button className="hero__next-btn">
          <img src={LeftIcon} alt="next" />
        </button>
        <ul className="hero__list">
          <li>
            <img src={HeroImg1} alt="avtomobil" />
          </li>
          <li>
            <img src={HeroImg2} alt="Недвижимость" />
          </li>
          <li>
            <img src={HeroImg3} alt="Электроника" />
          </li>
          <li>
            <img src={HeroImg4} alt="Работа" />
          </li>
          <li>
            <img src={HeroImg5} alt="Личные вещи" />
          </li>
          <li>
            <img src={HeroImg6} alt="Дом и Сад" />
          </li>
          <li>
            <img src={HeroImg7} alt="Стройка и ремонт" />
          </li>
          <li>
            <img src={HeroImg8} alt="Хобби и Спорт" />
          </li>
          <li>
            <img src={HeroImg9} alt="Бизнес и услуги" />
          </li>
        </ul>
        <button className="hero__next-btn">
          <img src={RightIcon} alt="next" />
        </button>
      </div>
    </div>
  );
}
