import "./footertop.scss";

import FooterIcon from "../../../assets/icons/footer-icon.svg";

import * as React from "react";
import Button from "@mui/material/Button";
import AppleIcon from "@mui/icons-material/Apple";
import ShopIcon from "@mui/icons-material/Shop";
import Stack from "@mui/material/Stack";

export function FooterTop({ bgColor }) {
  return (
    <div className={`footertwo ${bgColor}`}>
      <div className="container">
        <div className="footertop">
          <div className="footertop__left">
            <a href="#">
              <img src={FooterIcon} alt="kiwi" />
            </a>
            <p className="footertop__left-text">
              Продай, найди, купи все что пожелаешь…
            </p>
          </div>
          <p className="footertop__text">
            Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт
            обяления
          </p>
          <div className="footertop__right">
            <Stack direction="row" spacing={2}>
              <Button
                className="footertop__storebtn"
                variant="contained"
                sx={{
                  backgroundColor: "black",
                  color: "white",
                  padding: "15px 17px",
                  borderRadius: "27px",
                  gap: "15px",
                }}
                startIcon={<AppleIcon />}
              >
                Apple Store
              </Button>
            </Stack>
            <Stack direction="row" spacing={2}>
              <Button
                className="footertop__storebtn"
                variant="contained"
                sx={{
                  backgroundColor: "black",
                  color: "white",
                  padding: "15px 17px",
                  borderRadius: "27px",
                  gap: "15px",
                }}
                startIcon={<ShopIcon />}
              >
                GOOGLE PLAY
              </Button>
            </Stack>
          </div>
        </div>
      </div>
    </div>
  );
}
