import "./login.scss";
import KiwiImg from "../../assets/icons/login-kivi.svg";
import { useEffect, useRef, useState } from "react";
import { BaseUrl } from "../../constants/ConstantsUrl";
import axios from "axios";
import { Header } from "../../components/Header/Header";
import { HeroInfo } from "../../components/HeroInfo";

import { useLocation, useNavigate } from "react-router-dom";

export const Login = ({ setToken }) => {
  const loginRef = useRef(null);
  const passwordRef = useRef(null);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    const info = {
      email: loginRef.current.value,
      password: passwordRef.current.value,
    };

    console.log(info);
    axios
      .post(`${BaseUrl}/login`, info)
      .then((res) => {
        if (res.status === 200) {
          localStorage.setItem("mytoken", res?.data?.token);
          setToken(res?.data?.token);
          navigate("/");
        }
      })
      .catch((err) => {
        alert(err);
      });
  };

  return (
    <div>
      <Header />
      <HeroInfo />
      <div className="container">
        <div className="login">
          <div className="login__left">
            <h1 className="login__left-title">Ваша регистрация завершена!</h1>
            <p className="login__left-text">
              Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен
              в виде смс на ваше телефонный номер
            </p>
            <form onSubmit={handleSubmit} className="login__form">
              <label placeholder="Введите email или номер телефона">
                Введите email или номер телефона
              </label>
              <input
                ref={loginRef}
                className="login__input"
                type="text"
                placeholder="EMAIL"
              />
              <label placeholder="Пароль">Пароль</label>
              <input
                ref={passwordRef}
                className="login__input"
                type="password"
                placeholder="Пароль"
              />
              <button type="submit" className="login__submitBtn">
                Войти
              </button>
            </form>
            <div className="login__left-bottom">
              <span className="login__bottom-border"></span>{" "}
              <span className="login__left-bottomText">или</span>{" "}
              <span className="login__bottom-border"></span>
            </div>
            <button className="login__bottomBtn">
              Восстоновить ваше пароль
            </button>
          </div>
          <div className="login__right">
            <div className="login__right-inner">
              <img src={KiwiImg} alt="Kiwi" />
              <p className="login__right-text">
                Откройте для себя целую плошадку для продаже
              </p>
              <button className="login__right-btn">Как это делать?</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
