import "./Header.scss";
import Icon from "../../assets/icons/hedaer-logo.svg";
import ProfileIcon from "../../assets/icons/profile-icon.svg";

import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import { Link, Navigate, NavLink, useNavigate } from "react-router-dom";
import { Avatar } from "@mui/material";

export function Header() {
  const [value, setValue] = React.useState("one");
  const token = localStorage.getItem("mytoken");
  const navigate = useNavigate();

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const mytoken = localStorage.getItem("mytoken");
  return (
    <div className="top">
      <div className="container">
        <div className="header">
          <div className="header__left">
            <a href="#">
              <img src={Icon} alt="Kiwi-logo" />
            </a>
            <p className="header__left-text">
              Продай, найди, купи все что пожелаешь…
            </p>
          </div>
          <nav className="header__nav">
            <ul className="header__list">
              <li>
                <Link className="header__item" to="/">
                  Home
                </Link>
              </li>
              <li>
                <Link className="header__item">Магазины</Link>
              </li>
              <li>
                <Link className="header__item"> Для бизнеса</Link>
              </li>
              <li>
                <Link className="header__item">Помощь</Link>
              </li>
            </ul>
          </nav>

          <div className="header__lang">
            <span className="header__lang-item">Рус</span>
            <span className="header__lang-border"></span>
            <span className="header__lang-item">O’z</span>
          </div>
          <div className="header__right">
            <Box>
              <Tabs
                value={value}
                onChange={handleChange}
                aria-label="wrapped label tabs example"
              >
                <Tab
                  value="one"
                  label={
                    <button className="header__btn">
                      {token ? (
                        <Link to="/profile">
                          <Avatar alt="Remy Sharp" src={ProfileIcon} />
                        </Link>
                      ) : (
                        <NavLink className="header__btn-close" to={"/login"}>
                          Вход
                        </NavLink>
                      )}
                    </button>
                  }
                  wrapped
                />
                <span className="header__right-border"></span>
                <Tab
                  value="two"
                  label={
                    <div className="header__btn-profile">
                      {token ? (
                        <button
                          onClick={() => {
                            localStorage.removeItem("mytoken");
                          }}
                          className="header__btn"
                        >
                          <NavLink className="header__btn" to="/login">
                            <p>Log out</p>
                          </NavLink>
                        </button>
                      ) : (
                        <button className="header__btn">Регистрация</button>
                      )}
                    </div>
                  }
                />
              </Tabs>
            </Box>
          </div>
        </div>
      </div>
    </div>
  );
}
