import "./selectprops.scss";

import SelectIcon from "../../../assets/icons/select-icon.svg";

export const SelectProps = ({ text, title, text1, text2 }) => {
  return (
    <div>
      <select className="heroselect">
        <option value="1" className="selectdisabled">
          <img src={SelectIcon} alt="select" />
          {title}
        </option>
        <option value="2">{text}</option>
        <option value="3">{text1}</option>
        <option value="4">{text2}</option>
      </select>
    </div>
  );
};
