import "./home.scss";

import { useState, useEffect } from "react";
import scrollImg from "../../assets/image/scroll-img1.png";
import scrollImg2 from "../../assets/image/scroll-img2.png";
import scrollImg3 from "../../assets/image/scroll-img3.png";
import { Select } from "../../components/selects/Select";
import { SelectProps } from "../../components/selects/SelectProps";

import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import { HomeHero } from "../../components/HomeHero/HomeHero";
import { Header } from "../../components/Header/Header";
import { HeroInfo } from "../../components/HeroInfo";
import { Link, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { BaseUrlProducts } from "../../constants/ConstantsUrl";
import { Footer } from "../../components/Footer/Footer";
import { FooterTop } from "../../components/Footer/FooterTop";
import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@mui/material";

export const Home = () => {
  const [value, setValue] = useState("one");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const navigate = useNavigate();
  const loc = useLocation();
  const [products, setProducts] = useState([]);
  const [load, setLoad] = useState(false);
  useEffect(() => {
    axios
      .get(`${BaseUrlProducts}/products`)
      .then((res) => setProducts(res.data), setLoad(true))
      .catch((err) => {
        console.log(err);
      });
  }, []);

  if (!load) return <div>loading...</div>;

  return (
    <>
      <div className="home__middle">
        <div>
          <Header />
          <HeroInfo />
          <HomeHero />
        </div>
        <div className="container">
          <Box sx={{ width: "100%", paddingBottom: 2 }}>
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="wrapped label tabs example"
            >
              <Tab
                sx={{ fontWeight: 600 }}
                value="one"
                label="Новые объявления"
                wrapped
              />
              <Tab
                sx={{ fontWeight: 600 }}
                value="two"
                label="Лучщие предложение"
              />
            </Tabs>
          </Box>
        </div>
        <div className="container">
          <div>
            <Grid
              rowSpacing={1}
              columnSpacing={{ xs: 1, sm: 2, md: 3 }}
              className="products"
            >
              {products.slice(0, 16).map((item) => (
                <Link to={`product/${item.id}`}>
                  <Card sx={{ width: 265, height: 282, boxShadow: "none" }}>
                    <CardActionArea>
                      <CardMedia
                        component="img"
                        height="190"
                        width="262"
                        image={item.image}
                        alt={item.category}
                      />

                      <CardContent>
                        <Typography
                          variant="h5"
                          component="div"
                          className="home__card-title"
                        >
                          {item.category}
                        </Typography>
                        <Typography
                          variant="body2"
                          color="text.secondary"
                          fontWeight="700"
                        >
                          {item.price}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions>
                      <Typography variant="body2" color="text.secondary">
                        {item.price} sum
                      </Typography>
                    </CardActions>
                  </Card>
                </Link>
              ))}
            </Grid>
          </div>
          <hr />
        </div>

        <div className="container">
          <div className="home__select">
            <Select>
              <SelectProps
                title="Покозать еще"
                text="Плиточник"
                text1="Плиточник"
                text2="Плиточник"
              />
            </Select>
          </div>
        </div>

        <div className="home__scroll-img">
          <img src={scrollImg} alt="image" />
          <img src={scrollImg2} alt="image" />
          <img src={scrollImg3} alt="image" />
          <img src={scrollImg} alt="image" />
          <img src={scrollImg3} alt="image" />
          <img src={scrollImg2} alt="image" />
        </div>

        <div className="container">
          <Box sx={{ width: "100%", paddingBottom: 3, paddingTop: 3 }}>
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="wrapped label tabs example"
            >
              <Tab
                sx={{ fontWeight: 600 }}
                value="one"
                label="Просмотренные"
                wrapped
              />
              <Tab
                sx={{ fontWeight: 600 }}
                value="two"
                label="Выбор редакции"
              />
              <Tab
                sx={{ fontWeight: 600 }}
                value="three"
                label="Сниженные цены"
              />
            </Tabs>
          </Box>
        </div>
        <div className="container">
          <div>
            <Grid
              rowSpacing={1}
              columnSpacing={{ xs: 1, sm: 2, md: 3 }}
              className="products"
            >
              {products.slice(0, 4).map((item) => (
                <Link to={`product/${item.id}`}>
                  <Card sx={{ width: 265, height: 282, boxShadow: "none" }}>
                    <CardActionArea>
                      <CardMedia
                        component="img"
                        height="190"
                        width="262"
                        image={item.image}
                        alt={item.category}
                      />

                      <CardContent>
                        <Typography
                          variant="h5"
                          component="div"
                          className="home__card-title"
                        >
                          {item.category}
                        </Typography>
                        <Typography
                          variant="body2"
                          color="text.secondary"
                          fontWeight="700"
                        >
                          {item.price}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions>
                      <Typography variant="body2" color="text.secondary">
                        {item.price} sum
                      </Typography>
                    </CardActions>
                  </Card>
                </Link>
              ))}
            </Grid>
          </div>
          <hr />
        </div>

        <div className="container">
          <div className="home__select">
            <Select>
              <SelectProps
                title="Покозать еще"
                text="Плиточник"
                text1="Плиточник"
                text2="Плиточник"
              />
            </Select>
          </div>
        </div>
      </div>
      <FooterTop />
      <Footer />
    </>
  );
};
